import React, { useState, useEffect } from "react";
import "../App.css";
import { Button } from "./Button";
import "./HeroSection.css";
import Typist from "react-typist";

function HeroSection() {
  const [count, setCount] = useState(1);

  useEffect(() => {
    setCount(1);
  }, [count]);

  return (
    <div className="hero-container">
      <div className="image-container">
        <img src={require("../images/side-section.png").default} alt="" />
      </div>

      {/* <video src="/videos/banner.mp4" autoPlay loop muted /> */}
      <div className="content-container">
        <h1>Human centric designs for a scalable business growth.</h1>
        <p>
          Here at studio we work on
          {count ? (
            <Typist
              className="typing-texts"
              avgTypingDelay={100}
              onTypingDone={() => setCount(0)}
            >
              <span> Editorial Design.</span>
              <Typist.Backspace count={17} delay={2000} />
              <span> Web Design.</span>
              <Typist.Backspace count={11} delay={2000} />
              <span> Packaging Design.</span>
              <Typist.Backspace count={17} delay={2000} />
              <span> Branding.</span>
              <Typist.Backspace count={9} delay={2000} />
            </Typist>
          ) : (
            ""
          )}
        </p>
        <div className="hero-btns">
          <Button
            className="btns"
            buttonStyle="btn--primary"
            buttonSize="btn--large"
          >
            Start a Project
          </Button>
          <Button
            className="btns"
            buttonStyle="btn--primary"
            buttonSize="btn--large"
            aHref="tel:+919045187480"
          >
            Ring a Bell
          </Button>
        </div>
      </div>
      <div className="footer-landing">
        <p>
          © 2019-2020 Polardot Design Co., All rights reserved | P. and
          polardot.in and its logo are trademarks or registered trademarks in
          India and elsewhere.
        </p>
      </div>
    </div>
  );
}

export default HeroSection;
