import React from "react";
import "./ContactUs.css";
import { Button } from "./Button";

function ContactUs() {
  return (
    <div className="contact-us_container">
      <div className="container card message-container ">
        <form
          class="floating-form"
          action="mailto:email@email.com"
          method="post"
          encType="text/plain"
        >
          <div className="floating-label">
            <input
              class="floating-input"
              type="email"
              name="email"
              placeholder=" "
              required
            />
            <span class="highlight"></span>
            <label htmlFor="email" className="email">
              Email
              <p id="break"></p>
            </label>
          </div>
          <div className="floating-label">
            <input
              class="floating-input"
              type="text"
              name="subject"
              maxLength="45"
              placeholder=" "
            />
            <span class="highlight"></span>
            <label htmlFor="subject" className="subject">
              Subject
            </label>
          </div>
          <div className="floating-label">
            <textarea
              class="floating-input floating-textarea"
              name="message"
              cols="30"
              rows="7"
              required
              maxLength="500"
              placeholder=" "
            ></textarea>
            <span class="highlight"></span>
            <label htmlFor="message" className="message">
              Message
            </label>
          </div>
          <p className="button-container">
            <Button
              className="btns"
              buttonStyle="btn--primary"
              buttonSize="btn--small"
            >
              Send
            </Button>
          </p>
        </form>
      </div>
      <div className="footer-landing">
        <p>
          © 2019-2020 Polardot Design Co., All rights reserved | P. and
          polardot.in and its logo are trademarks or registered trademarks in
          India and elsewhere.
        </p>
      </div>
    </div>
  );
}

export default ContactUs;
